  }
  .borderLine{
    border: 0.5px solid #E5E5E5;
    /* width: 100%; */
    margin-top: 10px;
    margin-left: 15px;
    margin-right: 15px;
  }
//   /deep/.van-swipe__track {
//     height: 100%;
//     width: 100%;
//     display: flex;
//     /* overflow-x: auto; */
//     flex-flow: row nowrap;
// }
  /deep/.van-list__loading {
    display: none;
  }
  /deep/.van-pull-refresh__head {
    position: absolute;
    top: -65px;
    left: 0;
    width: 100%;
    height: 1.33333rem;
    overflow: hidden;
    color: #969799;
    font-size: 0.37333rem;
    line-height: 1.33333rem;
    text-align: center;
  }
</style>

<script>
  import { getAllOrders, getunevaluatedOrders, confirmOrder, cancelOrder, prepareToPayOrder, RepurchaseOrder,applyForAfter,applyRefund} from "../../services/orderListService";
  import { mapActions, mapState } from "vuex";
  import { Dialog } from 'vant';
  import Vue from 'vue';
  import netWorkError from "../../common/networkError/NetworkError"
  import { getDadaOrderTrack } from "../../services/expressTrajectory";
  export default
    {
      name: 'orderSwipeCard',
      props: ['mgChildKey','isActKey', 'isClickEven'],
      data: function () {
        return {
          img: {
            arrowShop: require("@/assets/images/mall/mall_icon_more.png"),
            callShop:require("@/assets/images/mall/call_small.png"),
            noList: require("@/assets/images/default/no_list_img.png"),//img里面没有找到
          },
          isdadaExpress:true, //判断是否是达达派送
          lists: [],
          isLists: false,//订单列表为空时显示
          goodsCount: 0,//一个订单内的商品种类数
          loading: false,//上拉加载状态
          finished: false,//上拉加载无数据时的状态
          totalPages: 5,//订单总页数
          page: 1,//当前列表的页数
          isRefresh: false,
          type: '',
          payParam: [],//准备支付请求回参
          ClickFlagEven: true,
          ifNetWorkError:false,//判断是否网络异常
          listMinHeight: document.body.clientHeight - this.$stbHeight - 55 -44,
          themeCurrent: this.$curTheme,
        }
      },
      components:{
        netWorkError
        },
      mounted() {
        //监听ios支付结果 检测当前订单状态
        // window.broadcaster.addEventListener("newPayBack", data2 => {
        //     console.log("ios++getUseOrderStatus");
        //     this.getOrderDetail();
        //     window.broadcaster.removeEventListener("newPayBack");
        // })
      },
      methods: {
        bottomAdTouchStart: function() {
          // console.log('111 touch start');
          this.adClickFlag = true;
        },
        bottomAdTouchMove: function() {
          // console.log('111 touch Move');
          this.adClickFlag = false;
        },
        bottomAdTouchEnd: function(adItem) {
          if (this.adClickFlag) {
            // console.log('111点击事件');
            this.ClickFlagEven = true;
          }
          else {
            // console.log('111滑动touch事件')
            this.ClickFlagEven = false;
          }
        },
        // 上拉加载
        onLoad() {
          if(this.mgChildKey!=this.isActKey){
            return
          }
          const _this = this;
          _this.page += 1;
          if (_this.totalPages >= _this.page) {
            if (_this.mgChildKey == '3') {
              _this.unevaluatedOrders();
            } else if (_this.mgChildKey == '2') {
              _this.getAllOrders('3');
            } else {
              _this.getAllOrders(_this.mgChildKey);
            }
            _this.finished = _this.page >= _this.totalPages ? true : false;
          } else if (_this.totalPages < _this.page || _this.lists != '') {
            _this.finished = true;
            _this.loading = false;
          }
        },
        // 下拉刷新
        onRefresh() {
          const _this = this;
          _this.page = 1;
          _this.lists = [];
          if (_this.mgChildKey == '3') {
            _this.unevaluatedOrders();
          } else if (_this.mgChildKey == '2') {
            _this.getAllOrders('3');
          } else {
            _this.getAllOrders(_this.mgChildKey);
          }
        },
        //清空数据
        emptyData(){
          var arr = [];
          arr.push(this.lists[0])
          this.lists = [];
        },
        //          获取订单列表
        getAllOrders(type) {
          const data = {
            page: this.page,
            row: 5,
            ownerCode: this.$userInfo.OWNER_CODE,
            billCate: type,//0代表全部类型的订单
            ownerPhone: this.$userInfo.PHONE,
          }
          getAllOrders(data).then(result => {
            this.totalPages = result.pages; //获取订单列表的总页数
            this.ifNetWorkError=false;
            if (result.total != 0) {//判断列表数据是否为空
              if(this.page == 1){
                this.lists =result.data
              }else{
                this.lists = this.lists.concat(result.data);//新加载的数据拼接在后面
              }
              this.lists.map((item) => {     //计算商品个数
                this.goodsCount = 0;
                // item.billCate=7;
                item.shop.skuList.map((skuItem) => {
                  this.goodsCount += skuItem.skuCount;
                  skuItem.picUrl=skuItem.spuPicUrl;//防止传给申请售后的图片为本地图片
                  Vue.prototype.$isHasLocalImage(skuItem.spuPicUrl,function(localImgUrl){
                  skuItem.spuPicUrl = localImgUrl;
              })
                });
                item.goodCount = this.goodsCount;
                for (let i = 0;i<item.shop.skuList.length;i++){
                  let skuItem = item.shop.skuList[i];
                  if (skuItem.expressCompany == '达达快递'){
                    item.hasExpressStatus=true;
                    return;
                  }
                }
              });
              console.log(this.lists)
              this.finished = this.page > this.totalPages ? true : false;
              this.loading = false;
              this.isLists = false;
              this.isRefresh = false;
            } else {
              this.isLists = true;
              this.finished = true;
              this.loading = false;
              this.isRefresh = false;
            }
          }).catch(error => {
          //加载失败后，关闭加载等待的文字
          this.ifNetWorkError = true;
          this.isRefresh=false;
          this.isLists=false;
          this.$toast("网络异常，请稍后重试！");
        });
        },

        //待评价列表
        unevaluatedOrders() {
          const data = {
            page: this.page,
            row: 8,
            ownerCode: this.$userInfo.OWNER_CODE,
            ownerPhone: this.$userInfo.PHONE,
          }
          getunevaluatedOrders(data).then(response => {
            this.totalPages = response.pages;
            // if (this.page == 1) { //如果是下拉刷新，从第一页开始刷新
            //   this.lists = [];
            // }
            if (response.resultCode == 0 && response.data.length != 0) {//判断数据是否为空
              this.lists = this.lists.concat(response.data);//新加载的数据拼接在后面
              this.lists.map((item) => {
                item.billCate2= 2;//给待评价订单新增一个字段，便于区分订单类别
                this.goodsCount = 0;
                item.shop.skuList.map((skuItem) => {
                  this.goodsCount += skuItem.skuCount;
                  skuItem.picUrl=skuItem.spuPicUrl;//防止传给申请售后的图片为本地图片
                  Vue.prototype.$isHasLocalImage(skuItem.spuPicUrl,function(localImgUrl){
                  skuItem.spuPicUrl = localImgUrl;
              })
                });
                item.goodCount = this.goodsCount;
              });
              // this.page += 1;
              this.isLists = false;
              this.loading = false;
              this.finished = this.page > this.totalPages ? true : false;
              this.isRefresh = false;
            } else {
              this.isLists = true;
              this.finished = true;
              this.loading = false;
              this.isRefresh = false;
            }
          })
        //   .catch(error => {
        //   //加载失败后，关闭加载等待的文字
        //   this.ifNetWorkError = true;
        //   this.isRefresh=false;
        //   this.isLists=false;
        //   this.$toast("网络异常，请稍后重试！");
        // });
        },
        //          确认收货
        confirmOrder(subBillNo) {
          if (!this.isClickEven) {
            return;
          }
             Dialog.confirm({
                    title:"您确认收到该订单商品了？",
                    confirmButtonText: "确认收货",
                    cancelButtonText:"取消"
                  }).then(() => {
                        const data = {
                          subBillNo: subBillNo,
                          signerType: '01',
                          signerCode: this.$userInfo.OWNER_CODE,
                        };
                        confirmOrder(data).then(response =>{
                          if (response.data == true) {
                            this.$toast("收货成功");
                            this.onRefresh(this.mgChildKey);
                          } else {
                            this.$toast("确认收货失败，请重新尝试");
                          }
                        });
                    }).catch(() => {
                      // on cancel
                      Dialog.close;
                    });

        },
        //去支付
        childPayFun(item) {
          if (!this.isClickEven) {
            return;
          }

          const data = {
            billNo: item.billNo
          }
          prepareToPayOrder(data).then(response => {
            if (response.resultCode == '0') {
              this.payParam = response.data;//请求回参
              this.payParam.billNo = item.billNo;
              //调用父视图支付
              this.$emit('childPayFun', this.payParam)
            }else{
              Dialog.alert({
              message:response.msg
            }).then(() => {

            });
            }
          });
        },
        //再次购买
        Repurchase(order, index) {
          if (!this.isClickEven) {
            return;
          }
          const data = [];
          if (index == 3) {//待评价列表中再次购买
            console.log("sds" + JSON.stringify(order));
            order.shop.skuList.map((item, index) => {
              data.push({
                ownerCode: this.$userInfo.OWNER_CODE,
                spuCode: item.spuCode,
                skuNo: item.skuNo,
                shopCode: order.shop.shopCode,
                skuCount: item.skuCount,
                mark: 1
              })
            });
             console.log("sds" + JSON.stringify(data));
          } else if (index == 4) {//待评价列表中再次购买
            data.push ({
              ownerCode: this.$userInfo.OWNER_CODE,
              spuCode: order.skuInfo.spuCode,
              skuNo: order.skuInfo.skuNo,
              shopCode: order.shopCode,
              skuCount: order.skuInfo.skuCount,
              mark: 1
            })
          }
          RepurchaseOrder(data).then(res => {
            if (res.resultCode == 0) {
              this.$toast(res.msg);
              this.$router.push({name:'shoppingCart',params:{data:data}});
            } else{           //补货中已下架商品不允许添加购物车并提示用户
             Dialog.alert({
              message:res.msg
            }).then(() => {

            });
            }
          });
        },
        //取消订单
        cancelOrder(billNo) {
          if (!this.isClickEven) {
            return;
          }
          Dialog.confirm({
                    title:"确认取消该订单？",
                    confirmButtonText: "确认取消",
                    cancelButtonText:"我再想想"
                  }).then(() => {
                    const data = {
                        billNo: billNo
                      }
                  cancelOrder(data).then(response => {
                    if (response.resultCode == "0") {
                      this.$toast("订单已取消");
                      this.onRefresh();
                    }
                });
              }).catch(() => {
                // on cancel
                Dialog.close;
              });
        },
        //评价订单
        toEvaluateOrder(item) {
          if (!this.isClickEven) {
            return;
          }

          console.log("待评价" + item);
          this.$router.push({
            name: 'myEvaluate', params: {
              detail: {
                // spuCode: item.skuInfo.spuCode,
                // shopCode: item.shopCode,
                // orderCode: item.skuInfo.subBilNo,
                // attrValueContent: item.skuInfo.attrValueContent,
                // spuPicUrl: item.skuInfo.spuPicUrl,
                billItem:item
              },
            }
          });
        },
        //跳转店铺首页
        toShop(shopCode) {
          if (!this.isClickEven) {
            return;
          }
          this.$router.push({ name: 'shopPage', params: { shopCode: shopCode } });
        },
        //跳转单店铺订单详情
        shopOrderDetail(item) {
           if (!this.isClickEven) {
            return;

          }
          const _this = this;
          const data = {
            ownerCode: _this.$userInfo.OWNER_CODE,
            billNo: item.billNo,
            subBillNo: item.shop.subBillNo,
            flag: item.billCate2,
            item: item,
            hasExpressStatus:item.hasExpressStatus,
          }

          this.$router.push({ name: 'orderDetails', params: { object: data } });
        },
        //跳转多店铺订单详情
        shopOrderDetailMulti(item){
          if (!this.ClickFlagEven || !this.isClickEven) {
            return;
          }
          // console.log('========>>>>>>>> 多店铺订单详情' +  this.isClickEven)
          const multiData = {
            ownerCode: this.$userInfo.OWNER_CODE,
            billNo: item.billNo,
            flag: item.billCate2,
            item: item
          }
          this.$router.push({ name: 'orderDetails', params: { object: multiData } });
        },
        // 跳转待评价订单详情
        evalOrderDetail(item) {
          if (!this.isClickEven) {
            return;
          }
          const data = {
            ownerCode: this.$userInfo.OWNER_CODE,
            billNo: item.billNo,
            subBillNo: item.billNo,
            flag: 5,
            item: item
          }
          this.$router.push({ name: 'orderDetails', params: { object: data } })
        },

        // 联系商家
        callShop(phone) {
          if (!this.isClickEven) {
            return;
          }
        //   window.location.href = "tel:" + phone;
        // },
         const platform = navigator.userAgent;
            const isiOS = !!platform.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
            if (isiOS) {
              window.location.href = "tel:" + phone;
            } else {
                Dialog.confirm({
                    title:"拨打电话",
                    message: phone,
                  }).then(() => {
                    window.location.href = "tel:" + phone;
                  }).catch(() => {
                    // on cancel
                    Dialog.close;
                  });
            }
      },
      // 申请售后按钮
      applyForAfter(item,skuItem,x){//0：普通订单 1：待评价订单
        let data={}
        let detail={}

       if(x==0){
         data={
            skuNo:skuItem.skuNo,
            billNo:item.saleOrderBillNo,
            subBillNo:item.billNo,
            ownerCode:this.$userInfo.OWNER_CODE,
            areaCode:this.$userInfo.AREA_CODE,
            spuCode:skuItem.spuCode,
            spuName:skuItem.spuName,
        }
        detail={
        skuNo: skuItem.skuNo,
        saleOrderBillNo: item.saleOrderBillNo ,
        saleOrderSubBillNo: item.shop.subBillNo,
        shopCode: item.shop.shopCode,
        shopName: item.shop.shopName,
        spuCode: skuItem.spuCode,
        spuName: skuItem.spuName,
        attrValueContent: skuItem.attrValueContent,
        skuPrice: skuItem.skuPrice,
        skuCount:skuItem.skuCount,
        picUrl: skuItem.picUrl,//防止本地图片传图
        spuPicUrl:skuItem.spuPicUrl,
        returnRealTotal:skuItem.realSkuTotal,
        }
       }else{
         data={
            skuNo:item.skuInfo.skuNo,
            billNo:item.skuInfo.billNo,
            subBillNo:item.skuInfo.subBilNo,
            ownerCode:this.$userInfo.OWNER_CODE,
            areaCode:this.$userInfo.AREA_CODE,
            spuCode:item.skuInfo.spuCode,
            spuName:item.skuInfo.spuName,
        }
        detail={
        skuNo: item.skuInfo.skuNo,
        saleOrderBillNo:item.skuInfo.billNo,
        saleOrderSubBillNo:item.skuInfo.subBilNo,
        shopCode: item.shopCode,
        shopName: item.shopName,
        spuCode: item.skuInfo.spuCode,
        spuName: item.skuInfo.spuName,
        attrValueContent: item.skuInfo.attrValueContent,
        skuPrice: item.skuInfo.skuPrice,
        skuCount:item.skuInfo.skuCount,
        spuPicUrl:item.skuInfo.spuPicUrl,
        picUrl:item.skuInfo.picUrl,//防止本地图片传图
        returnRealTotal:item.skuInfo.realSkuTotal,
        }
       }
        applyForAfter(data).then(response=>{
          if(response.resultCode=='0'){
             this.$router.push({name: 'afterSaleApply',params:{detail:detail}});
             console.log("申请售后"+JSON.stringify(detail));
          }else if(response.resultCode=='20100'){
             Dialog.alert({
                    message: response.msg
                  }).then(() => {
                    // 确定
                });
          }
        })
      },
        //查看物流信息
        gotoMapforExpress(item){
          console.log("===== "+JSON.stringify(item));
          // '314378699124482048C2',this.$userInfo.AREA_CODE
            this.$router.push({ name: "mapForExpress", params: { subBillNo: item.billNo } });

        },
      // 待收货列表申请退款
      applyRefund(subBillNo){
        const data={
          subBillNo:subBillNo,
          ownerCode: this.$userInfo.OWNER_CODE,
        }
         Dialog.confirm({
                    title:"确认退款吗？",
                    message:"退款后券无法退回哦",
                    confirmButtonText: "确认退款",
                    cancelButtonText:"我再想想"
                  }).then(() => {
        applyRefund(data).then(response=>{
          if(response.resultCode=='0'){
            this.$toast("申请已提交");
            this.onRefresh();
          }else{
            this.$toast(response.msg)
          }
        }).catch(error=>{
          this.$toast(response.msg)
        })
      }).catch(()=>{

      })
    }
  }
    }

</script>
