<template>
  <div class="myOrderList" >
    <!-- <van-nav-bar class="title" title="我的订单" @click-left="goToBack()" left-arrow :style="setTopHeight"></van-nav-bar> -->
     <navigation-bar class="title" @onLeftClick="goToBack()" pageName="我的订单" :style="setTopHeight"/>
    <div class="tabTitle" :style="padingtopNum">
      <van-tabs v-model="active" :swipe-threshold="6" color="#FB5768" @click="controlTab" line-width="0.67rem"
        line-height="0.08rem">
        <van-tab v-for="(tit,i) in tabTitle" :title="tit" :key="i" :title-style="themeCurrent == '1' ? {fontSize: '17px'} : {}"></van-tab>
      </van-tabs>
    </div>
    <!-- <van-pull-refresh v-model="isPushLoading" @refresh="onRefresh"> -->
    <van-swipe class="orderSwipe" :show-indicators="false" :loop="false" @change="controlSwiper"
      :initial-swipe="orderType" ref="swipe">

      <van-swipe-item :key="0" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"  @touchend="bottomAdTouchEnd()">
        <div :style="{'height':'1px'}"></div>
        <orderSwipeCard ref="mychild0" :isActKey="active" :isClickEven="swipeClickedEven" mgChildKey="0" @childPayFun="goToPay">

        </orderSwipeCard>
      </van-swipe-item>

      <van-swipe-item :key="1" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"  @touchend="bottomAdTouchEnd()">
        <div :style="{'height':'1px'}"></div>
        <orderSwipeCard ref="mychild1" :isActKey="active" :isClickEven="swipeClickedEven" mgChildKey="1" @childPayFun="goToPay">

        </orderSwipeCard>
      </van-swipe-item>
      <van-swipe-item :key="2" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"  @touchend="bottomAdTouchEnd()">
        <div :style="{'height':'1px'}"></div>
        <orderSwipeCard ref="mychild2" :isActKey="active" :isClickEven="swipeClickedEven" mgChildKey="2">

        </orderSwipeCard>
      </van-swipe-item>
      <van-swipe-item :key="3" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"  @touchend="bottomAdTouchEnd()">
        <div :style="{'height':'1px'}"></div>
        <orderSwipeCard ref="mychild3" :isActKey="active" :isClickEven="swipeClickedEven" mgChildKey="3">

        </orderSwipeCard>
      </van-swipe-item>

      <van-swipe-item :key="4" @touchstart="bottomAdTouchStart()" @touchmove="bottomAdTouchMove()"  @touchend="bottomAdTouchEnd()">
        <div :style="{'height':'1px'}"></div>
        <orderSwipeCard ref="mychild4" :isActKey="active" :isClickEven="swipeClickedEven" mgChildKey="4">

        </orderSwipeCard>
      </van-swipe-item>
    </van-swipe>
    <!-- </van-pull-refresh> -->
    <!-- 去支付时显示微信或者支付宝支付 -->
    <van-popup v-model="payWindowShow" position="bottom" class="vanPopup" @close="closePopup">
      <div class="payModeBox">
        <div class="payModeTitle">支付方式</div>
        <div v-show="isAcppay" class="payModeItem" @click="alipay(2)">
          <div style="display: flex;align-items: center;height: 100%;">
            <img src="@/assets/images/common/icon_unipay.png" alt="">
            <div>云闪付</div>
            <img class="iconUnipayRight" src="@/assets/images/common/icon_unipay_right.png" alt="">
            <div class="saleIcon" v-if="isAcpPayTag">{{acpPayTag}}</div>
          </div>
        </div>
        <div v-show="isAlipay" class="payModeItem" @click="alipay(1)">
          <img src="@/assets/images/common/icon_alipay.png" alt="">
          <div>支付宝</div>
        </div>
        <div v-show="isWechart" class="payModeItem" @click="alipay(0)">
          <img src="@/assets/images/common/icon_wechart.png" alt="">
          <div>微信</div>
        </div>
      </div>
      <div class="cancelBtn" @click="closePopup">取消</div>
    </van-popup>
  </div>
</template>

<style scoped="scoped" lang="scss">
  .myOrderList {
    width: 100%;
    background: #f9f9f9;
    height: 100%;
    display:flex;
    flex-direction:column;
  }
  .myOrderList .title {
    z-index: 100;
    width: 100%;
  }

  .myOrderList .commonHead {
    width: 25px;
    padding: 20px 20px 10px 20px;
    float: left;
  }

  .myOrderList .orderTitle {
    font-family: PingFangSC-Medium;
    font-size: 20px;
    color: #333333;
    letter-spacing: -0.06px;
    float: right;
    padding-top: 20px;
    padding-right: 140px;
  }

  .tabTitle {
    width: 100%;
    /* z-index: 100; */
    background: #FFFFFF;
  }
  .orderSwipe {
   overflow-x: hidden;
   overflow-y: auto;
   height:100%;
  }

  .van-tabs {
    position: relative;
    padding: 0px;
    line-height: 60px!important;
  }
 /deep/.van-tab {
     line-height: 60px!important;
   }
   /deep/.van-hairline--top-bottom::after, .van-hairline-unset--top-bottom::after{
  border-width: 0 0;
}
  .vanPopup {
    max-height: 327px;
    background: transparent;
    padding: 0 8px 8px;
    /* padding-bottom: 8px; */
    box-sizing: border-box;

    .payModeBox {
      width: 359px;
      max-height: 251px;
      background: #fff;
      margin: 0 auto;
      border-radius: 4px;
      margin-bottom: 8px;

      .payModeTitle {
        padding: 0;
        text-align: center;
        height: 61px;
        line-height: 61px;
        border-bottom: 1px solid #ECECEC;
        font-family: PingFangSC-Medium;
        font-size: 16px;
        color: #3A3A3A;
        letter-spacing: 0.06px;
      }

      .payModeItem {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 60px;
        border-color: #ECECEC;
        border-bottom: 1px solid #e5e5e5;

        img {
          width: 26px;
          height: 26px;
        }

        div {
          font-family: PingFangSC-Regular;
          font-size: 16px;
          color: #3A3A3A;
          letter-spacing: 0.06px;
          margin-left: 6px;
        }
      }

      .payModeItem:last-child {
        border-bottom: 0px;
      }
    }

    .cancelBtn {
      width: 359px;
      height: 60px;
      line-height: 60px;
      text-align: center;
      border-radius: 4px;
      background: #fff;
      margin: 0 auto;
      font-family: PingFangSC-Regular;
      font-size: 16px;
      color: #3A3A3A;
      letter-spacing: 0.06px;
    }
  }
  /deep/.van-tabs__line {
    position: absolute;
    bottom: 20px;
    left: 0;
    z-index: 1;
    height: 0.08rem;
    background-color: #ee0a24;
    border-radius: 0.08rem;
}
  .iconUnipayRight{
    margin-left: 5px;
  }
  .saleIcon{
    border: 1px solid #FB5364;
    border-radius: 2px;
    padding: 0px 2px 0px 2px;
    font-size: 12px !important;
    color: #FB5364 !important;
    margin-left: 5px;
    position: absolute;
    left: 245px;
  }
</style>

<script>
  import orderSwipeCard from './orderSwipeCard.vue';
  import { mapActions, mapState } from "vuex";
   import { Toast, Dialog } from "vant";
   import NavigationBar from '@/common/currency/NavigationBar.vue';
   import { prepareToPayOrderService, orderDetailService, unionOrderPrepayService } from '../../services/productOrderService.js';
  export default {
    name:'myOrderList',
    components: { orderSwipeCard, NavigationBar},
    data: function () {
      return {
        tabTitle: ['全部', '待付款', '待收货', '待评价', '已完成'],
        active: 0,
        key: 0,
        swipeClickedEven: true,
        payWindowShow: false,//去支付弹窗
        isAlipay: false,//是否支付宝支付
        isWechart: false,//是否微信支付
        isAcppay:false,//云闪付支付选择
        acpPayTag:'',//满减标签
        isAcpPayTag:false,//是否有满减标签
        payParam: {},//支付参数
        orderType: this.$route.query.orderType*=1,
        returnFlag:false,
        setTopHeight:{
          'padding-top':this.$stbHeight+'px'
          },
        padingtopNum :{'top': this.$stbHeight +47+"px"},
        themeCurrent: this.$curTheme,
      }
    },
    mounted() {

    },
    methods: {
      bottomAdTouchStart: function() {
        // console.log('touch start');
        this.adClickFlag = true;
      },
      bottomAdTouchMove: function() {
        // console.log('touch Move');
        this.adClickFlag = false;
      },
      bottomAdTouchEnd: function(adItem) {
        if (this.adClickFlag) {
          // console.log('点击事件');
          this.swipeClickedEven = true;
        }
        else {
          // console.log('滑动touch事件')
          this.swipeClickedEven = false;
        }
      },
      //          通过滑动和点击切换订单类别
      controlTab(index) {
        const _this = this;
        _this.key = index;        //同步swipe和tabbar的选中状态
        _this.$refs.swipe.swipeTo(index);
        window.scrollTo(0, 0);//切换swipe时回到页面顶端
        // _this.$refs.mychild.onFresh(index);
      },
      controlSwiper(index) {
        const _this = this;
        _this.active = index;
        console.log("当前页" + _this.active);
        if (index == 0) {
          _this.$refs.mychild0.onRefresh();
          _this.$refs.mychild1.emptyData();
          _this.$refs.mychild2.emptyData();
          _this.$refs.mychild3.emptyData();
          _this.$refs.mychild4.emptyData();
        } else if (index == 1) {
          _this.$refs.mychild1.onRefresh();
          _this.$refs.mychild0.emptyData();
          _this.$refs.mychild2.emptyData();
          _this.$refs.mychild3.emptyData();
          _this.$refs.mychild4.emptyData();
        } else if (index == 2) {
          _this.$refs.mychild2.onRefresh();
          _this.$refs.mychild1.emptyData();
          _this.$refs.mychild0.emptyData();
          _this.$refs.mychild3.emptyData();
          _this.$refs.mychild4.emptyData();
        } else if (index == 3) {
          _this.$refs.mychild3.onRefresh();
          _this.$refs.mychild1.emptyData();
          _this.$refs.mychild2.emptyData();
          _this.$refs.mychild0.emptyData();
          _this.$refs.mychild4.emptyData();
        } else {
          _this.$refs.mychild4.onRefresh();
          _this.$refs.mychild1.emptyData();
          _this.$refs.mychild2.emptyData();
          _this.$refs.mychild0.emptyData();
          _this.$refs.mychild3.emptyData();
        }
        window.scrollTo(0, 0);//切换swipe时回到页面顶端
      },
      goToPay(val) {
        this.payParam = val;
        if (1 == this.payParam.IS_ALI_PAY) {//判断是否支持微信或者支付宝支付
          this.isAlipay = true;
        } else {
          this.isAlipay = false;
        };
        if (1 == this.payParam.IS_WX_PAY) {
          this.isWechart = true;
        } else {
          this.isWechart = false;
        };
        if (1 == this.payParam.IS_ACP_PAY) {
          this.isAcppay = true;
        } else {
          this.isAcppay = false;
        };
        if (this.payParam.acpPayTag != "" && this.payParam.acpPayTag != undefined &&this.payParam.acpPayTag != null){
          this.acpPayTag = this.payParam.acpPayTag;
          this.isAcpPayTag = true;
        }
        //显示支付弹窗
        this.payWindowShow = true;
      },
      //关闭去支付弹窗
      closePopup() {
        this.payWindowShow = false;
      },
      /*银联下单支付*/
      alipay(index) {//x订单编码//y支付金额,index 0 微信 1 支付宝 p 支付参数
        if (index===2){
          let pram = {
            'merOrderId':this.payParam.merOrderId,
            'payType':2
          }
          unionOrderPrepayService(pram).then(res =>{
            console.log(res);
            let unipay = cordova.plugins.LantuUnionPay;
            //payMode 00 正式环境，01测试环境 正式版本需要切换
            unipay.pay({tn:res.data.tn,payMode:this.payParam.acpGatewayType},(success)=>{
              console.log("支付成功回调"+JSON.stringify(success));
              this.getOrderDetail();
            },(error)=>{
              console.log("支付失败回调"+JSON.stringify(error));
              this.getOrderDetail();
            });
          }).catch(error => {
            console.log(error);
          });

        }else {
          this.payParam.payWay = index;
          this.payWindowShow = false;
          //监听ios支付结果 检测当前订单状态
          if (this.$devicePlatform != "pc") {

              console.log("ios++addEventListener");

            window.broadcaster.addEventListener("orderListPayBack", this.payCallBack);
          }
          window.broadcaster.fireNativeEvent("ionicToNative", {
            DATA: this.payParam,
            FLAG: '678'
          }, function () {
          })
        }
      },
      //支付完成后回调
      payCallBack() {
          this.getOrderDetail();
      },
      // 支付完成后获取订单详情查询订单状态
      getOrderDetail: function () {
        var data = {
          billNo: this.payParam.billNo,
          ownerCode: this.$userInfo.OWNER_CODE
        }
        console.log("getOrderPayDetail");
        orderDetailService(data).then(res => {
          if ("0" == res.resultCode) {//true表示返回成功
            if (res.data.billStatus == "02") {

               /* 取消监听，避免重复监听 */
              if (this.$devicePlatform != "pc") {
                window.broadcaster.removeEventListener("orderListPayBack",this.payCallBack);
              }
              this.$router.push({ name: "paySuccess", params: { orderData: res.data } });
              console.log("支付成功");
            } else {
              //其他情况跳转订单列表页
              console.log("支付失败");
            }
          } else {
            //其他情况跳转订单列表页
            console.log("查询订单状态失败");
          }
        }).catch(error => {
        });
      },

      goToBack() {
        if (this.$route.query.flag == 10||this.$route.query.flag=='10') {//需要跳转到优品首页
          this.$router.push({ path: '/productPage' });
        } else {
          this.$router.go(-1);//返回上一层
        }
      },
      //设置订单返回标志---用于支付返回的物理返回按键
      // setOrderFlag(flag){
      //   console.log(flag)
      //   this.$store.commit("orderFlagChange", {
      //       flag: flag
      //   });
      // },
      //设置离开订单列表时的tab
      setTabIndex(tabIndex,tabFlag){
        this.$store.commit("leaveOrderList",{
          tabIndex:tabIndex,
          // tabFlag:tabFlag
        })
      }
    },
    mounted: function () {
      this.returnFlag=this.$route.query.ifPayReturn;
      console.log("正常订单点请求" +this.$route.query.ifPayReturn+this.$route.query.flag);
      if (this.$route.query.flag === 10 && this.returnFlag==true) {//如果是从支付结果页面跳转回我的订单，默认显示全部订单
      console.log("----------"+this.returnFlag);
        this.controlSwiper('0');
        this.orderType=0;
        this.returnFlag=false;
      }else if(this.$route.params.orderType==0||this.$route.params.orderType){//如果从我家首页进入订单列表
        console.log("正常请求" + this.$route.params.orderType);
        this.orderType = this.$route.params.orderType;
        this.controlSwiper(this.orderType);//点击订单状态进入列表
      }else{
        console.log("1111111----------"+this.returnFlag+this.$route.query.flag);
        this.orderType=this.tabIndex;
         console.log("返回"+this.orderType);
        this.controlSwiper(this.orderType);
      }
    },
    beforeRouteLeave(to, from, next){
    if(to.name!='minePage'){
      this.setTabIndex(this.active);
    //  this.tabFlag=true;
    }else{
      // this.tabFlag=fasle;
    }
    next();
  },
  computed:{
     tabIndex(){
        return this.$store.state.productPage.tabIndex;
      },
      ...mapState({
        orderListEventBackButtonFlag: state => state.goBack.orderListEventBackButtonFlag
      })
  },
  watch: {
    /*监控到物理返回键点击状态*/
    orderListEventBackButtonFlag(val) {
      if (val) {
        if(this.payWindowShow==true){
          this.payWindowShow=false;
        }else{
          this.goToBack();
        }
        this.$store.dispatch('setOrderListEventBackButtonFlag', false);
      }
    }
  },
}
</script>
